@use 'sass:math';

$font-size: 16px;

.docs-typography {
    font-size: $font-size;
    line-height: math.div(26px, $font-size);
    position: relative;

    a {
        color: #e8175d;
    }

    figure {
        margin: 45px 0;

        img {
            display: block;
            position: relative;
            left: 50%;
            transform: translateX(-50%);
        }

        figcaption {
            margin-top: 15px;
            text-align: center;
            font-size: 13px;
            color: #808080;
        }
    }

    code:not(.hljs) {
        padding: 0 4px;
        border-radius: 1.5px;
        background-color: #ebebeb;
        color: #595959;
    }

    pre {
        code:not(.hljs) {
            display: block;
            font-size: 13px;
            background-color: #ebebeb;
            border-radius: 1.5px;
            color: #595959;
            padding: 4px 10px;
        }
    }

    code.hljs {
        background-color: #f5f5f5;
        font-size: 13px;
        border-radius: 1.5px;
        padding: 4px 10px;
    }

    table, td, th {
        border: 1px solid #d9d9d9;
    }

    th {
        background-color: #f2f2f2;
    }

    td, th {
        padding: 6px 14px;
    }
}
