html, body {
    margin: 0;
    padding: 0;
    height: 100%;
}

body {
    font-family: 'Roboto', sans-serif;
    font-size: 15px;
    line-height: 1;
    color: #262626;
}

a {
    color: inherit;
    text-decoration: none;
}

table {
    border-collapse: collapse;
}

td, th {
    padding: 0;
    margin: 0;
}

@import 'docs-markup';
@import 'docs-nav';
@import 'docs-content';
@import 'docs-section';
@import 'docs-typography';
@import 'docs-alert';
@import 'docs-meta';
@import 'docs-logo';