.docs-nav {
    position: fixed;
    background-color: #333;
    color: #fff;
    width: 220px;
    height: 100%;
    overflow-y: auto;
}
.docs-nav__logo {
    padding: 25px 15px 35px;
}
.docs-nav__links {
    list-style: none;
    margin: 0;
    padding: 0;
}
.docs-nav__link {
    padding: 10px 15px;
    display: block;

    &:hover,
    &--current {
        //background-color: #66ccff;
        //text-shadow: 0 1px 3px rgba(#000, .2);
        background-color: rgba(#fff, .1);
    }
    &--current {
        cursor: default;
    }
}